.titulo-proyectos {
    text-align: center;
    font-size: 3.5rem;
    margin-bottom: 40px;
    color: #1ce497;
    text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.7);
}

.proyectos-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    max-width: 1100px;
    margin: 0 auto;
    padding: 20px;
}

.proyecto-card {
    background: rgba(255, 255, 255, 0.05);
    padding: 20px;
    border-radius: 12px;
    backdrop-filter: blur(4px);
    transition: transform 0.3s ease;
    color: #eee;
}

.proyecto-card:hover {
    transform: translateY(-5px);
}

.proyecto-card h2 {
    text-align: center;
    color: #1ce497;
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.proyecto-card p {
    font-size: 1.5rem;
    text-align: justify;
    color: #ddd;
    margin-bottom: 20px;
}

.btn-ver {
    width: 100%;
    padding: 8px 0;
    border: none;
    background: #1ce497;
    color: #111;
    border-radius: 8px;
    font-size: 2rem;
    cursor: pointer;
    font-weight: 600;
    transition: 0.3s ease;
}

.btn-ver:hover {
    background: #13b87a;
}

.modal-fondo {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.75);
    backdrop-filter: blur(5px);
    z-index: 900;
}

.modal-contenedor {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    max-width: 90%;
    max-height: 90%;
    overflow-y: auto;
}

.modal-contenido {
    background: white;
    padding: 25px;
    border-radius: 15px;
    position: relative;
    box-shadow: 0 0 20px rgba(0,0,0,0.4);
}

.cerrar {
    position: absolute;
    top: 8px;
    right: 10px;
    background: red;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 4px 7px;
}

/* Responsivo */
@media (max-width: 992px) {
    .proyectos-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 600px) {
    .proyectos-grid {
        grid-template-columns: 1fr;
    }
}